@import "../../global";

.menu {
  position: fixed;
  top: 70px; // Adjust based on your topbar height
  right: 0;
  width: 300px;
  height: calc(100vh - 70px);
  background-color: white;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 1s ease;

  &.active {
    right: 0;
  }

  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 30px;
    font-weight: 300;
    color: $mainColor;
    width: 60%;

    li {
      margin-bottom: 25px;
      a {
        font-size: inherit;
        color: inherit;
        text-decoration: none;
      }
    }
  }
}